import React from "react";
import {
  getREADMEfromMyGithub,
  getProjectImageUrl
} from "../../util/projectData";
import { NextPageContext } from "next";
import MarkdownLayout from "../../src/MyMarkdownLayout";
import SimpleSnackbar from "../../src/MySnack";

type Props = {
  markdown: string;
  imageUrl: string;
  snack: boolean;
};

type IState = {
  snack: boolean;
}

class ProjectPage extends React.Component<Props, IState> {
  constructor(props: Props) {
    super(props);
    this.state = {snack: true};
  }

  static async getInitialProps({ query }: NextPageContext) {
    const markdown = await getREADMEfromMyGithub(query.pid as string);
    const imageUrl = getProjectImageUrl(query.pid as string);
    const snack = false
    if(query.pid == "ar-admin-page" || query.pid == "chat-webapp-node-js"){
      setState({snack: false});
    }
    return { markdown, imageUrl,  };
  }

  render() {
    return (
      <React.Fragment>
        { true &&
          <SimpleSnackbar />
          }
        <MarkdownLayout
          projectMD={this.props.markdown}
          backroundImage={this.props.imageUrl}
        />
      </React.Fragment>
    );
  }
}

export default ProjectPage;
